import { Controller, Commands } from '@ali/4ever-cangjie';

import { getCurrentBlocks } from '../utils';
import { IGetParagraphColor } from '../types';

const getParagraphBgColor = (controller: Controller, payload: IGetParagraphColor) => {
  const { node: currentBlock } = payload;
  const blocks = getCurrentBlocks(controller, currentBlock.key);

  let color = '';
  if (Array.isArray(blocks) && blocks.length > 0) {
    color = controller.query('getCommonShd').fill;
  }

  return color;
};

export default getParagraphBgColor;
